.projects {
  display: flex;
  justify-content: space-evenly;
}
.project-card {
  width: 35%;
  border: 2px solid grey;
  background-color: aliceblue;
  margin: 2%;
  color: black;
}
img {
  width: 100%;
}
.proj-title {
  padding: 1%;
  border-bottom: 1px solid black;
}
.proj-dsc {
  padding: 1%;
  border-bottom: 1px solid black;
}

.proj-tech {
  display: flex;
  padding: 1%;
  border-bottom: 1px solid black;
}
.tech-list {
  width: 40%;
  list-style-type: none;
  margin: 0% 0% 0% 5%;
}
li {
  margin: 0% 0% 0% 10%;
}
.proj-links {
  padding: 1%;
}

h3 {
  border-bottom: 1px solid black;
}
@media only screen and (max-width: 900px) {
 
    .project-card {
      width: 35%;
      border: 2px solid grey;
      background-color: aliceblue;
      margin: 2%;
      color: black;
    } 
  }
  @media only screen and (max-width: 700px) {
 
    .project-card {
      width: 90%;
      border: 2px solid grey;
      background-color: aliceblue;
      margin: 4%;
      color: black;
    } 

    .projects {
      display: flex;
      justify-content: space-evenly;
      flex-direction: column;
    }
  }